* {
  box-sizing: border-box;
}
.App {
  display: flex;
}
.produktion {
  margin-left: 3.5rem;
}
.formfält label,
h2 {
  margin: 0;
  font-size: 18px;
  font-weight: 500;
}
input {
  width: 100%;
  padding: 7px;
  border: 1px solid gray;
}
.tjänster {
  display: flex;
}
.first-uppdelning {
  margin: 0 5px 0.5rem 0;
  width: 100%;
}
button {
  border-radius: 2px;
  padding: 5px 10px;
}
.first-uppdelning button {
  align-self: flex-start;
  color: black;
}
.andra-uppdelning button {
  color: blue;
  border: 1px solid blue;
}